.switch {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-left: $spacing-medium;
  width: 50px;
  height: fit-content;
  border-radius: $border-radius *5;
  border: none;
  background-color: $grayscale-4;
  cursor: pointer;
  outline: none;
  &::after {
    content: "";
    display: block;
    width: 24px;
    height: 24px;
    position: absolute;
    background-color: $grayscale-1;
    top: -1px;
    left: 0;
    right: unset;
    border-radius: 100px;
    transition: .3 ease all;
    box-shadow: 0px 0px 2px 2px rgba(0,0,0, .2);
  }
  &.active {
    background-color: $alert;
    &::after {
      right: 0;
      left: unset;
    }
  }
  i {
    line-height: 20px;
    display: block;
    background: none
  }
}
