.navbar {
  position: fixed;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $grayscale-1;
  transition: 300ms ease all;
  z-index: $z-index-navBav;
  @include shadow($grayscale-3);
  @include wrapper(0);
  img {
    height: 50px;
  }
  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__burger {
    display: none;
  }
  body.dark & {
    background-color: $grayscale-5;
    @include shadow($grayscale-4)
  }
}

@media screen and(max-width: $tablet) {
  .navbar {
    &__nav {
      display: none;
    }
    &__burger {
      display: block;
    }
  }
}

@media screen and(max-width: $phone) {
  .navbar {
    img {
      height: 45px;
    }
  }
}