.home {
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: $spacing-large;
  @include wrapper($spacing-large*4 $spacing-medium*2)
}

@media screen and(max-width: $tablet) {
  .home {
    gap: $spacing-medium;
  }
}

