.header {
  position: fixed;
  top: 80px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $grayscale-2;
  transition: 300ms ease all;
  @include wrapper($spacing-small);
  h3 {
    color: $primary;
    letter-spacing: .02rem;
  }
  body.dark & {
    background-color: $grayscale-6;
    h1 {
      color: $grayscale-3;
    }
  }
}

@media screen and(max-width: $tablet) {
  .header {
    h1 {
      font-size: 1.5rem;
    }
    h3 {
      font-size: 1rem;
      font-weight: normal;
    }
  }
}

@media screen and(max-width: $phone) {
  .header {
    &__switch {
      display: none;
    }
  }
}
